import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { withRouter } from "react-router";
import FileUploader from "Component/FileUploader";
import ReactExport from "react-export-excel";
import "./index.scss";

const ExcelFile = ReactExport.ExcelFile;
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet;
const ExcelColumn = ReactExport.ExcelFile.ExcelColumn;
const Upload = (props) => {
  const dispatch = useDispatch();
  const [isDisplayProgress, setIsDisplayProgress] = React.useState(false);
  const Field = [
    {
      dataField: "id", //field ini wajib ada untuk editable column
      text: "ID",
      editable: false,
      sort: true,
      hidden: true,
      type: "number",
    },
    { dataField: "areaName", text: "Area", type: "string", sort: true },
    {
      dataField: "wmsPoSkuCode",
      text: "PO SKU Code",
      type: "number",
      sort: true,
    },
    {
      dataField: "skuNumber",
      text: "SKU Number",
      type: "number",
      sort: true,
    },
    {
      dataField: "expiryDateCol",
      text: "Expiry Date",
      type: "date_indo",
      sort: true,
    },
    { dataField: "grade", text: "Grade", type: "string", sort: true },
    {
      dataField: "qty",
      text: "Input Weight/Quantity",
      type: "number",
      sort: true,
    },
    { dataField: "newGrade", text: "Waste Type", type: "string", sort: true },
    {
      dataField: "reason",
      text: "Note",
      type: "string",
      sort: true,
    },
  ];

  const dataSet1 = [
    {
      area: "",
      po: "",
      skuNumber: "",
      ripenessLevel: "",
      qty: "",
      grade: "",
      reason: "",
    },
  ];

  return (
    <div className="mb-5">
      {/* export this table to excel */}
      <ExcelFile
        element={
          // dont change id and style please
          <button id="download-template-xls" style={{ display: "none" }}>
            Download Data
          </button>
        }
        file
        name
        without
        types
        filename="WasteUpload_Template"
      >
        <ExcelSheet data={dataSet1} name="template">
          <ExcelColumn label="Area" value="area" />
          <ExcelColumn label="PO SKU CODE" value="po" />
          <ExcelColumn label="SKU Number" value="skuNumber" />
          <ExcelColumn label="Expiry Date" value="expiryDate" />
          <ExcelColumn label="Grade" value="ripenessLevel" />
          <ExcelColumn label="Input Weight/Quantity" value="qty" />
          <ExcelColumn label="Waste Type" value="grade" />
          <ExcelColumn label="Note" value="reason" />
        </ExcelSheet>
      </ExcelFile>
      {/* end export this table to excel */}

      <FileUploader
        nextPage={() =>
          props.history.push("/inventory-entry/waste/CSV-upload/review")
        }
        transaction={"waste"}
        field={Field}
      />
    </div>
  );
};

export default withRouter(Upload);
